<template>
  <div>
    <div id="veto_socket" v-if="!ws">
      <h3 class="my-3">Подключение</h3>
      <div class="container">
        <div class="d-flex justify-content-between">
          <input type="text" aria-label="IP сервера" class="form-control form-control-lg mr-3" v-model="wsurl"/>
          <button class="btn btn-lg btn-outline-info ml-3 mb-3 w-100" @click="connectWS">Подключиться к серверу вето
          </button>
        </div>
      </div>
    </div>
    <div id="veto_team" v-if="ws && veto && !team">
      <h3 class="my-3">Выбери свою команду</h3>
      <div class="container">
        <div class="d-flex justify-content-between" v-if="veto.teams">
          <button class="btn btn-lg btn-outline-info mr-2 mb-3 w-100" @click="team = veto.teams[0]">{{
              veto.teams[0]
            }}
          </button>
          <button class="btn btn-lg btn-outline-info mr-2 mb-3 w-100" @click="team = veto.teams[1]">{{
              veto.teams[1]
            }}
          </button>
          <button class="btn btn-lg btn-outline-info mr-2 mb-3 w-100" @click="team = 'observer'">
            Observer
          </button>
        </div>
      </div>
    </div>
    <div id="veto_wait" v-if="ws && team && veto && veto.status === 'w'">
      <h3 class="my-3">Команда {{ team }}</h3>
      <div class="container">
        <h5>Ожидание открытия голосования</h5>
        <button class="btn btn-lg btn-outline-info mr-2 mb-3 w-100" v-if="team === 'observer'"
                @click="ws.send('start')">
          Начать голосование
        </button>
      </div>
    </div>
    <div id="veto_veto"
         v-if="ws && veto && team && team !== 'observer' && (veto.status === 'p' || veto.status === 'b') && veto.teams[veto.team] === team">
      <h3 class="my-3">Команда {{ team }}</h3>
      <div class="container">
        <div class="row row-cols-1">
          <div class="col-auto" v-for="(p, techname) in maps" :key="p.name" v-show="!p.tech">
            <Map v-bind="p"/>
            <div class="d-flex">
              <button class="btn btn-lg btn-outline-info w-100 mb-3" @click="vetoMap(techname)"
                      v-if="!p.ban && !p.pick && !p.decider">
                {{ veto.status === 'p' ? "Пик" : "Бан" }}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="veto_vetowait"
         v-if="ws && veto && team && team !== 'observer' && (veto.status === 'p' || veto.status === 'b') && veto.teams[veto.team] !== team">
      <h3 class="my-3">Команда {{ team }}</h3>
      <div class="container">
        <h5>Подождите, пока другая команда голосует</h5>
      </div>
    </div>
    <div id="veto_results" v-if="ws && veto && team && (veto.status === 'd' || team === 'observer')">
      <h3 class="my-3">Результаты</h3>
      <div class="container">
        <div class="row row-cols-1">
          <Map class="col-auto" v-for="p in maps" :key="p.name"
               v-show="!p.tech" v-bind="p"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Map from "@/components/molecules/Map";
import MapPool from "@/constants/MapPool";

export default {
  name: "VetoPanel",
  components: {Map},
  data: () => ({
    team: null,
    veto: null,
    results: [],
    ws: null,
    wsurl: "ws://10.10.174.42:7777/"
  }),
  computed: {
    pool() {
      return MapPool
    },
    maps() {
      if (!this.veto.maps)
        return [];
      const mappool = {};
      for (const mapName in this.veto.maps) {
        if (!Object.prototype.hasOwnProperty.call(this.veto.maps, mapName))
          continue;
        const map = this.pool[mapName];
        if (this.veto.maps[mapName].status !== "free")
          map[this.veto.maps[mapName].status] = this.veto.teams[this.veto.maps[mapName].team];
        if (this.veto.status === "d" && this.veto.maps[mapName].status === "free")
          map["decider"] = true;
        mappool[mapName] = map;
      }
      return mappool;
    }
  },
  methods: {
    connectWS() {
      this.ws = new WebSocket(this.wsurl);
      this.ws.onmessage = (event) => {
        this.veto = JSON.parse(event.data);
      };
      this.ws.onopen = () => {
        this.ws.send("");
      };
    },
    vetoMap(mapname) {
      if (this.veto.status === 'p' || this.veto.status === 'b')
        this.ws.send(`${this.veto.status === 'p' ? "pick" : "ban"};${this.team};${mapname}`);
    }
  },
  beforeDestroy() {
    if (this.ws)
      this.ws.close();
  }
}
</script>
