// https://api.steampowered.com/ISteamUser/GetPlayerSummaries/v2/

import {firstBy} from 'thenby'

const getJSONfromURL = async (url) => {
    let result = await fetch(url)
    return await result.json()
}

export async function getCommonData() {
    return await getJSONfromURL("https://api.npoint.io/e5e375d909598a210915")
}

export const teamRatingSortRule = firstBy("p", "desc").thenBy("m", "desc").thenBy("w", "desc").thenBy("l", "desc").thenBy("rd", "desc").thenBy((it) => (it.team.name))
export const scoreboardSortRule = firstBy((it) => (it.deaths - it.kills)).thenBy((it) => (it.info.nickname))
export const playerRatingSortRule = firstBy((it) => (it.rating.deaths - it.rating.kills)).thenBy((it) => (it.info.nickname))

